.jcc-timeline-item {
}

.jcc-timeline-item__title {
  @include responsive-h5();
  @include u-font-family("sans");
  position: relative;
}

.jcc-timeline-item__excerpt {
  @include t-copy();
  @include u-line-height("body", 4);
  @include u-padding-right(4);
}

.jcc-timeline-item__media {
  @include media-icon();

  @include at-media(tablet) {
    position: absolute;
    top: 0;
  }
}
